"use client";

import React, { useEffect, useState } from "react";
import Card from "./Card";
import { User } from "@/interfaces/userInterface";
import axios from "axios";
import Image from "next/image";

interface UserInterfaceProps {
  backendName: string;
}

const UserInterface = ({ backendName }: UserInterfaceProps) => {
  const apiUrl = process.env.NEXT_PUBLIC_API_URL || "http://localhost:8000";
  const [users, setUsers] = useState<User[]>([]);
  const [newUsers, setNewUsers] = useState({ name: "", email: "" });
  const [updateUser, setUpdateUser] = useState({ id: "", name: "", email: "" });

  useEffect(() => {
    const fetchUsers = async () => {
      try {
        const res = await axios.get(`${apiUrl}/api/${backendName}/users`);
        setUsers(res.data.reverse());
      } catch (error) {
        console.log("Error fetching users:", error);
      }
    };
    fetchUsers();
  }, [apiUrl, backendName]);

  const createUser = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    try {
      const res = await axios.post(
        `${apiUrl}/api/${backendName}/users`,
        newUsers
      );
      setUsers([res.data, ...users]);
      setNewUsers({ name: "", email: "" });
    } catch (error) {
      console.log("Error creating user:", error);
    }
  };

  return (
    <div className={`p-4 bg-sky-400 min-h-screen ${backendName}`}>
      <Image src="/gologo.svg" alt="Logo" width={100} height={100} />

      <form onSubmit={createUser} className="mb-4 space-y-2">
        <input
          type="text"
          placeholder="Name"
          value={newUsers.name}
          onChange={(e) => setNewUsers({ ...newUsers, name: e.target.value })}
          className="border p-2 rounded"
        />
        <input
          type="email"
          placeholder="Email"
          value={newUsers.email}
          onChange={(e) => setNewUsers({ ...newUsers, email: e.target.value })}
          className="border p-2 rounded"
        />
        <button
          type="submit"
          className="bg-blue-500 text-white px-4 py-2 rounded"
        >
          Create User
        </button>
      </form>

      <h1 className="text-2xl font-bold">Users</h1>

      <div className="space-y-2">
        {users.map((user) => (
          <div key={user.id}>
            <Card user={user} />
            <button className="mt-2 bg-red-500 text-white px-4 py-2 rounded">
              Delete
            </button>
          </div>
        ))}
      </div>
    </div>
  );
};

export default UserInterface;
